.single-product {
    border-bottom: 1px solid grey;
    margin: 2%;
    padding: 2%;
    transition: .2s;
    cursor: pointer;
    width: 95%;
    height: 60%;
    display: flex;
    border-radius: 3%;
    box-shadow: 0 0 20px rgba(0,0,0,0.3);

    @media (max-width: 700px) {
        flex-direction: column;
    } 

    button {
        color: #fff;
        background: #16a085;
        margin-left: 10px;
        width: 150px;
        height: 40px;
        font-size:22px;
        float:right;
        border-radius: 35%;
    }

    div {
        padding: 3%;
        display: block;
    }

    .first {
        flex: 1;
    }

    .second {
        flex: 3;
        display: block;
    }

    .price {
        font-size: 22px;
    }
    .relat {
        position: relative;
    }

    .extra {
        text-align: right;
        position: absolute;
        top: 50px;
        right: 47px;
        font-weight: bold;
        color: hotpink;
        height: 30px;
        width: 100px;
        padding: 5px auto;
    }
}

